import React from 'react'
// import { useHttpClient } from '../../../shared/hooks/http-hook';
import Card from '../../../shared/components/UIElements/Card'
import Forminput from './Forminput';

const dummy_propduct= [
  {id:'p1', name:'FOOD', description:"Delicious Food", image:'https://th.bing.com/th/id/OIP.MQL8JAtYTc7xXAM23md0dAHaEo?pid=ImgDet&rs=1'},
  {id:'p2', name:'CLOTH', description:'Perfect Clothes', image:'https://th.bing.com/th/id/OIP.Ihjwnj9-gzPw_xWZAvUn9gHaFU?pid=ImgDet&rs=1'},
  {id:'p3', name:'EV', description:'Electric Vehicle',image:'https://th.bing.com/th/id/R.50a024a69d57ca18eef4e752c6c3124d?rik=Ie5TI%2fKofwjabw&riu=http%3a%2f%2fwww.hdcarwallpapers.com%2fwalls%2f2011_honda_ev_ster_concept_3-wide.jpg&ehk=HutjsjBBv7PEtBQGK8mLwc53ClegzaUA7H8c%2bDH8Jqk%3d&risl=&pid=ImgRaw&r=0'},
  {id:'p4', name:'INFRASTRACTURE', description:'Imagination',image:'https://th.bing.com/th/id/OIP.QJt7X8H8E1ktIh5S254RkQHaEw?pid=ImgDet&rs=1'},
  {id:'p5', name:'DRON', description:'Flying Machine',image:'https://th.bing.com/th/id/R.a4b761a09d5ddb3fcc293d5798d97c01?rik=3sFVokViF6hlgg&riu=http%3a%2f%2fwww.highlandradio.com%2fwp-content%2fuploads%2f2016%2f04%2fdrone.jpg&ehk=akw7tYH2tvZBhmI3cPozLSaKkv2VR3c5ueYBmC1qFD4%3d&risl=&pid=ImgRaw&r=0'},
  {id:'p6', name:'DECORATION', description:'Beautiful Creativity',image:'https://th.bing.com/th/id/OIP.oT_RKawMogxaEoIx--vAkQHaMa?pid=ImgDet&rs=1'},
 
]
const Allproductlist = () => {

  if (dummy_propduct.length === 0) {
    return (
      <div className="center">
        <Card>
          <h2>No users found</h2>
        </Card>
      </div>
    );
  }
  return (
    <>
    {
        dummy_propduct.map((p)=>
        <Forminput
          key={p.id}
          id={p.id}
        //  productcreator={p.creator}
            name={p.name}
            title={p.title}
            image={p.image}
            description={p.description}
            // price={p.price}
        />
        )
    }
        
    </>
  )
}

export default Allproductlist
